"use strict";
import tryParseCurrencyAmount from "lib/utils/tryParseCurrencyAmount";
import { Text } from "ui/src";
import { useUSDCValue } from "uniswap/src/features/transactions/hooks/useUSDCPrice";
import { NumberType, useFormatter } from "utils/formatNumbers";
export function BaseQuoteFiatAmount({
  price,
  base,
  quote,
  variant
}) {
  const { formatCurrencyAmount, formatPrice } = useFormatter();
  const quoteCurrencyAmount = tryParseCurrencyAmount(price?.toFixed(), price?.quoteCurrency);
  const usdPrice = useUSDCValue(quoteCurrencyAmount);
  if (!price || !base || !quote) {
    return null;
  }
  return <><Text variant={variant ?? "body3"} color="$neutral1">{formatPrice({ price, type: NumberType.TokenTx })} {quote?.symbol} = 1 {base?.symbol}</Text>{" "}<Text variant={variant ?? "body3"} color="$neutral2">
        (
        {formatCurrencyAmount({
    amount: usdPrice,
    type: NumberType.FiatTokenPrice
  })}
        )
      </Text></>;
}
